.student-bot {
  width: 100vw;
  max-width: 480px;
  min-width: 260px;
  background: #fff;
  margin: 48px auto 0;
  border-radius: 12px;
  box-shadow: 0 6px 30px rgba(109, 79, 194, 0.12);
  display: flex;
  flex-direction: column;
  border: 1px solid #dadada;
  font-family: 'Inter', Arial, sans-serif;
  height: 90vh;
  min-height: 390px;
}

.chat-window {
  background: #f4f0ff;
  padding: 20px 10px;
  flex: 1;
  min-height: 200px;
  height: 100%;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  gap: 13px;
}

.chat-message {
  display: flex;
  flex-direction: column;
  max-width: 80%;
}

.chat-message.bot {
  text-align: left; /* Align bot messages to the left */
}

.chat-message.student {
  align-self: flex-end; /* Align student messages to the right */
}

.chat-message .message-text {
  word-break: break-word;
  background: #f6f2ff;
  border-radius: 14px 14px 14px 4px;
  padding: 12px 16px;
  color: #222;
  font-size: 1rem;
  box-shadow: 0 2px 8px rgba(155, 120, 255, 0.04);
}

.chat-message.student .message-text {
  background: #6d4fc2;
  color: #fff;
  border-radius: 14px 14px 4px 14px;
}

.chat-form {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 12px 13px;
  border-top: 1px solid #d0c8f0;
  background: #fff;
}

.chat-input {
  flex: 1;
  padding: 11px 15px;
  border: 1px solid #d1cdfa;
  border-radius: 20px;
  font-size: 1.05rem;
  background: #fafaff;
  outline: none;
  transition: border 0.2s;
  text-align: left;
}

.chat-button {
  padding: 9px 22px;
  background: #6d4fc2;
  color: #fff;
  border: none;
  border-radius: 21px;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  box-shadow: 0 2px 8px rgba(109, 79, 194, 0.08);
  transition: background 0.15s, box-shadow 0.15s;
}

@media (max-width: 950px) { /* Laptop/Tablet */
  .student-bot {
    width: 100vw;
    max-width: 96vw;
    min-width: 0;
    margin: 10px auto 0;
    border-radius: 0;
    box-shadow: none;
  }
  .chat-message .message-text {
    max-width: 97vw;
  }
}

@media (max-width: 600px) { /* Mobile */
  .student-bot {
    width: 100vw;
    height: 100dvh;
    max-width: 100vw;
    min-width: 0;
    margin: 0;
    border-radius: 0;
    box-shadow: none;
    border: none;
  }
  .chat-window {
    padding: 7px 1vw;
    min-height: 120px;
  }
  .chat-message .message-text {
    max-width: 95vw;
    font-size: 1.03rem;
    padding: 10px 10px;
  }
  .chat-form {
    padding: 7px 2vw;
  }
}

/* Scrollbar styling */
.chat-window::-webkit-scrollbar {
  width: 7px;
  background: #ebecf5;
}

.chat-window::-webkit-scrollbar-thumb {
  background: #d5c7ff;
  border-radius: 12px;
}
